import 'uni-pages';import '@dcloudio/uni-stat';import Vue from 'vue'
import App from './App'

// 引入MinRouter文件
import MinRouter from './MinRouter'
// 引入router文件
import minRouter from './router'
import EjooooCache from './EjooooCache'
import MinRequest from './MinRequest'
import minRequest from './api'
import store from './store'

Vue.config.productionTip = false

// 注册路由
Vue.use(MinRouter)
// 注册缓存器
Vue.use(EjooooCache)
// 设置默认缓存时间
// Vue.use(EjooooCache, {timeout: 600})

// 注册请求
Vue.use(MinRequest)

App.mpType = 'app'

const app = new Vue({
    ...App,
	store:store,
    minRouter,
    minRequest
})
app.$mount()